<template>
	<div class="container">
        <!-- 图片循环 -->
        <ul>
            <li v-for="(item,index) in images.localIds">
                <img @click="previewImage(index)" :src="item">
                <i class="cubeic-wrong" @click="removeImage(index)"></i>
            </li>
        </ul>
        <!-- 图片选择 -->
        <div v-show="num!=images.localIds.length" class="chooseImage" @click="chooseImage">
            <div>
                <i class="fa fa-camera" aria-hidden="true"></i>
                <span>{{intro}}</span>
            </div>
        </div>
	</div>
</template>
<script type="text/javascript">
import Vue from 'vue';
import axios from 'axios';
import {JSSDKURL} from "@/api";
//jssdk
import wx from 'weixin-js-sdk';
export default{
	name:"JSSDK",
    props:{
        intro:{
            type:String,
            default:"身份证正面"
        },
        num:{
            type:Number,
            default:10
        }
    },
	data(){
		return {
            images:{localIds:["http://img3.imgtn.bdimg.com/it/u=3381573685,1866477444&fm=26&gp=0.jpg"]},
            initialIndex:0,
            params:{}
		}
	},
	async created(){
        let tUrl = encodeURIComponent(window.location.href.split('#')[0]); //需要跳转到哪个页面就写哪个页面的链接
        let datas=await JSSDKURL({"url":tUrl});
        let data=await axios.get("https://api-uat.tianan-life.com/wx-claim-apply/api/callBack/Test?url="+tUrl)
        //let data=JSON.parse(datas.data.replace(/\'/g,"\""));
        data=data.data;
        wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: data.appId, // 必填，公众号的唯一标识
            timestamp: data.timestamp, // 必填，生成签名的时间戳
            nonceStr: data.nonceStr, // 必填，生成签名的随机串
            signature: data.signature,// 必填，签名
            jsApiList: ['previewImage','getLocalImgData','chooseImage'] // 必填，需要使用的JS接口列表
        });
        wx.error(res=>{
            console.log(res)
        })
	},
	methods:{
		/**
         * 选择图片
         */
        chooseImage() {
            let _this = this;
            wx.chooseImage({
                "sizeType": ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
                "sourceType": ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
                "count": _this.num-_this.images.localIds.length>5?5:_this.num-_this.images.localIds.length, // 默认9
                success: function (res) {
                    _this.iosPreview(res.localIds)
                },
                fail: function() {

                }
            });
        },
        /**
         * 预览图片   采用cubeui(jssdk需要http链接)
         */
        previewImage(index){
            this.initialIndex = index
            this.params = {
                $props: {
                    imgs: this.images.localIds,
                    initialIndex: 'initialIndex', // 响应式数据的key名
                    loop: true
                },
                $events: {
                    change: (i) => {
                        // 必须更新 initialIndex
                        this.initialIndex = i
                    },
                    hide:()=>{
                        
                    }
                }
            }
            this.$createImagePreview({ ...this.params }).show()
            /*wx.previewImage({
                current: this.images.localIds[index], // 当前显示图片的https链接
                urls: this.images.localIds,// 需要预览的图片https链接列表
            })*/
        },
        /**
         * 删除图片
         */
        removeImage(index){
            this.images.localIds.splice(index,1)
        },
        /*
        *个人认为这属于在for循环里添加了一个异步请求类型
        */
        async iosPreview(localIds) {
            for(var i = 0; i < localIds.length; i++) {
                await this.readImage(localIds[i]);
            }
        },
        readImage(item) {
            let _this = this;
            return new Promise((resolve, reject) => {
                wx.getLocalImgData({
                    localId: item,
                    success: function(res) {
                        let localData = res.localData;
                        //第一个替换的是换行符，第二个替换的是图片类型，因为在IOS机上测试时看到它的图片类型时jgp，
                        //这不知道时什么格式的图片，为了兼容其他设备就把它转为jpeg
                        if (localData.indexOf('data:image') != 0) {
                            //判断是否有这样的头部
                            localData = 'data:image/jpeg;base64,' +  localData
                        }
                        localData = localData.replace(/\r|\n/g, '').replace('data:image/jgp', 'data:image/jpeg')
                        _this.images.localIds.push(localData);
                        resolve('done!');
                    },
                    fail: function() {

                    }
                })
            })
        }
	},
	mounted(){
		
	},
}
</script>
<style type="text/css" lang="stylus">
    .container{
        .chooseImage{
            height:104px;
            width:134px;
            float:left;
            border:1px solid #ccc;
            margin: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
            &>div{
                text-align: center;
                display: flex;
                flex-direction: column;
                &>span{
                    margin-top: 5px;
                    color: #ccc
                }
                .fa-camera{
                    font-size:25px;
                }
            }
        }
        ul{
            li{
                position: relative;
                height:104px;
                width:134px;
                float: left;
                margin: 5px;
                border:1px solid #ccc;
                img{
                    height:100%;
                    width:100%;
                }
                .cubeic-wrong{
                    position: absolute;
                    z-index: 2;
                    top: -2px;
                    right: -2px;
                }
                .cubeic-wrong:before{
                    display: inline-block;
                    -webkit-transform: scale(1.625);
                    transform: scale(1.625);
                    -webkit-transform-origin: center;
                    transform-origin: center;
                }
            }
        }
    }
</style>


